<template>
  <a-modal
    id="addUserModal"
    :title="isEdit ? '' : ''"
    v-model="visible"
    @ok="handleOk"
    width="800px"
    :footer="null"
  >
    <a-form class="kfc" :form="form" @submit="handleSubmit">
      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
          required
        >
          <a-radio-group
            defaultValue="def"
            buttonStyle="solid"
            :value="isVip ? 'vip' : 'def'"
            @change="onUserTypeChange"
          >
            <a-radio-button value="def"></a-radio-button>
            <a-radio-button value="vip"></a-radio-button>
          </a-radio-group>
        </a-form-item>
      </a-row>
      <a-row>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'nick',
                { rules: [{ required: true, message: '' }] }
              ]"
              placeholder="(20)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              minlength="6"
              :type="isEdit ? 'password' : 'text'"
              v-decorator="['password', passwordRules]"
              placeholder="(6)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="200"
              v-decorator="['email', emailRules]"
              placeholder=""
            />
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="10"
              v-decorator="[
                'realName',
                { rules: [{ required: false, message: '' }] }
              ]"
              placeholder="(10)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'position',
                { rules: [{ required: false, message: '' }] }
              ]"
              placeholder="(20)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="11"
              v-decorator="['mobile', phoneRules]"
              placeholder="(11)"
            />
          </a-form-item>
        </a-col>
      </a-row>

      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-input
            maxlength="50"
            v-decorator="[
              'orgName',
              { rules: [{ required: true, message: '' }] }
            ]"
            placeholder="(50)"
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-input
            maxlength="80"
            :disabled="!isVip"
            v-decorator="[
              'configValue',
              {
                rules: [{ required: true, message: '' }],
                initialValue: ''
              }
            ]"
            placeholder="(80)"
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-input
            :disabled="!isVip"
            maxlength="50"
            v-decorator="[
              'domain',
              {
                rules: [{ required: true, message: '' }],
                initialValue: 'lingxe.anbai.com'
              }
            ]"
            placeholder="(50)"
          />
        </a-form-item>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-input-number
            :min="10"
            :max="999"
            style="width:100%"
            v-decorator="[
              'siteCount',
              { rules: [{ required: true, message: '' }] }
            ]"
            placeholder=" 10999"
          />
        </a-form-item>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-input-search
            disabled
            v-model="oldAddress"
            v-if="hideCascader"
            @search="hideCascader = false"
          >
            <a-button slot="enterButton"></a-button>
          </a-input-search>

          <a-cascader
            v-if="!hideCascader"
            v-decorator="['cascader', cascaderRules]"
            :options="options"
            changeOnSelect
            :loadData="loadCascaderData"
            @change="onCascaderChange"
            placeholder=""
          />
        </a-form-item>

        <a-row class="ant-form-item" v-if="cascaderResult !== null">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-input
              v-decorator="['details', detailsAddressRules]"
              maxlength="200"
              v-model="details"
              placeholder="(200)"
            />
          </a-col>
        </a-row>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-tree-select
            v-decorator="[
              'permissionIds',
              {
                rules: [{ required: true, message: '' }],
                initialValue: [17]
              }
            ]"
            style="width: 100%"
            :treeData="authModule"
            treeCheckable
            @change="onChangeAuth"
            :showCheckedStrategy="SHOW_PARENT"
            searchPlaceholder=""
          />
        </a-form-item>
        <br />
        <a-form-item
          v-if="isVip"
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="logo"
        >
          <div class="clearfix">
            <a-upload
              v-decorator="[
                'logo',
                { rules: [{ required: false, message: 'logo' }] }
              ]"
              name="logo"
              listType="picture-card"
              :customRequest="uploadPng"
              :fileList="fileList"
              @preview="handleUploadPreview"
              @change="handleUploadChange"
            >
              <div>
                <a-icon type="plus" />
                <div class="ant-upload-text">Upload</div>
              </div>
            </a-upload>
            <!--  -->
            <a-modal
              :visible="previewVisible"
              :footer="null"
              @cancel="previewVisible = false"
            >
              <img alt="example" style="width: 100%" :src="previewImage" />
            </a-modal>
          </div>
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-radio-group
            v-decorator="[
              'userStatus',
              { rules: [{ required: true }], initialValue: 1 }
            ]"
          >
            <a-radio :value="1"></a-radio>
            <a-radio :value="0"></a-radio>
          </a-radio-group>
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-radio-group
            v-decorator="[
              'accountType',
              { rules: [{ required: true }], initialValue: 0 }
            ]"
            @change="onAccountTypeChange"
          >
            <a-radio :value="0"></a-radio>
            <a-radio :value="2"></a-radio>
          </a-radio-group>
        </a-form-item>

        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-date-picker
              :showToday="false"
              format="YYYY-MM-DD"
              placeholder=""
              :value="deadlineEndDate"
              @change=" date => this.deadlineEndDate = date || null ">
              <template slot="renderExtraFooter">
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment()}" ></a-checkable-tag>
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment().add(3, 'month')}" ></a-checkable-tag>
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment().add(6, 'month')}" ></a-checkable-tag>
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment().add(1, 'years')}" ></a-checkable-tag>
              </template>
            </a-date-picker>
          </a-col>
        </a-row>

        <br />
        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-button @click="visible = false"></a-button>&nbsp;&nbsp;&nbsp;&nbsp;
            <a-button type="primary" @click="handleSubmit">{{ isEdit ? "" : "" }}</a-button>
          </a-col>
        </a-row>
      </a-row>
    </a-form>
  </a-modal>
</template>

<script>
import moment from "moment";
import "moment/locale/zh-cn";
moment.locale("zh-cn");

// import cascader from "../plugins/china.json";
import {
  phoneRules,
  emailRules,
  passwordRules,
  cascaderRules,
  detailsAddressRules
} from "../plugins/data.js";

import { addUser, updateUser, getCite, getRSA, getPicture } from "../api/api";

import { TreeSelect } from "ant-design-vue";
const SHOW_PARENT = TreeSelect.SHOW_PARENT;
export default {
  data() {
    return {
      isVip: false,
      phoneRules,
      emailRules,
      passwordRules,
      cascaderRules,
      detailsAddressRules,
      SHOW_PARENT,
      visible: false,
      form: this.$form.createForm(this),
      formItemLayout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 }
      },
      formItemLayout2: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      hideCascader: false,
      isEdit: false,
      uid: null,
      record: null,
      isTestuser: false, // ,
      options: [], // 
      cascaderResult: null, //  
      oldAddress: null,
      details: null, // 
      authModule: [], // 
      autoAuth: [17],
      previewVisible: false, // logo 
      previewImage: "", // logo 
      uploadImage: null, //  Base64
      fileList: [], // 
      deadlineEndDate: null, // 
    };
  },
  mounted() {
    this.$nextTick(() => {
      // To disabled submit button at the beginning.
      this.form.validateFields();
    });
  },
  watch: {
    visible: function(value) {
      if (value === false) {
        this.form.resetFields();
        this.isVip = false;
        this.deadlineEndDate = null;
        this.uploadImage = null;
        this.orgAddress = null;
        this.isEdit = false;
        this.hideCascader = false; // 
        this.oldAddress = null; // 
        this.isTestuser = false;
        this.fileList = [];
        this.uid = null;
      }
    }
  },
  methods: {
    onChangeAuth(value) {
      // console.log("onChange ", value);
      this.$nextTick(() => {
        if (value[0] !== 18) {
          this.form.setFieldsValue({
            permissionIds: value.concat([17])
          });
        }
      });
    },
    onUserTypeChange(e) {
      this.isVip = e.target.value === "vip";
      this.form.setFieldsValue({
        configValue: this.isVip ? "" : "", // 
        domain: this.isVip ? "" : "lingxe.anbai.com" // 
      });
    },
    async getLogo(userId){
      try {
        let res = await getPicture({ userId });
        console.log(res);
        if (!res.valid) this.$message.error(res.msg);
        if (res.data && res.data.imageInfo) {
          this.fileList = [
            {
              uid: "-1",
              name: "old",
              status: "done",
              url: res.data.imageInfo
            }
          ];
        }
      } catch (e) {
        window.console.log(e);
      }
    },
    initForm(record, auth) {
      // 
      this.record = record;
      this.uid = record.user_id;
      this.isEdit = true; // 
      this.hideCascader = true; // 
      this.oldAddress = record.user_address; // 
      this.isTestuser = record.account_type === 2; // 
      if (record.deadline_end_date) {
        this.deadlineEndDate = moment(record.deadline_end_date); // 
      }
      //  Logo
      if (record.domain && record.domain !== "lingxe.anbai.com"){
        this.getLogo(record.user_id);
        this.isVip = true;
      }

      setTimeout(() => {
        this.form.setFieldsValue({
          nick: record.nick, // 
          password: record.password, // 
          mobile: record.mobile, // 
          email: record.email, // 
          realName: record.real_name, // 
          position: record.position, // 
          orgName: record.org_name, // 
          configValue: record.platform_name || "", // 
          domain: record.domain || "lingxe.anbai.com", // 
          siteCount: record.site_count, // 
          userStatus: record.user_status, // 
          accountType: record.account_type, // 
          permissionIds: auth.map(i => i.permission_id) // 
        });
      });
    },
    moment,
    range(start, end) {
      const result = [];
      for (let i = start; i < end; i++) result.push(i);
      return result;
    },
    disabledDate(current) {
      // Can not select days before today and today
      return current && current < moment().endOf("day");
    },

    disabledDateTime() {
      return {
        disabledHours: () => this.range(0, 24).splice(4, 20),
        disabledMinutes: () => this.range(30, 60),
        disabledSeconds: () => [55, 56]
      };
    },
    async loadCascaderData(selectedOptions) {
      try {
        const targetOption = selectedOptions[selectedOptions.length - 1];
        targetOption.loading = true;
        // console.log(targetOption);

        let res = await getCite({
          postCode: targetOption.value
        });

        if (res.valid) {
          targetOption.loading = false;
          targetOption.children = res.data.map(item => {
            return {
              value: item.post_code,
              label: item.city_name,
              deep: targetOption.deep + 1,
              isLeaf: targetOption.deep + 1 > 1
            };
          });

          this.options = [...this.options];
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    async apiGetCite() {
      try {
        let res = await getCite();
        if (res.valid) {
          this.options = res.data.map(item => {
            return {
              value: item.post_code,
              label: item.city_name,
              deep: 0,
              isLeaf: false
            };
          });
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    //
    onAccountTypeChange() {
      this.$nextTick(() => {
        let bool = Number(this.form.getFieldValue("accountType"));
        this.isTestuser = bool === 2;
      });
    },
    // 
    uploadPng() {
      // console.log("uploadPng", fileObj.file);
    },
    // 
    handleUploadChange(data) {
      console.log("handleUploadChange", data);
      // 
      if (typeof FileReader === "undefined") {
        return alert("IE10+");
      }
      // 
      if (!data.file.originFileObj) return (this.fileList = []);
      //
      let file = data.file.originFileObj;

      if ( !['image/png','image/jpeg'].includes( file.type) || file.size / 1024 /
      1024 > 1) {
        return this.$message.error("1MBJPGPNG!");
      }

      const reader = new FileReader();
      reader.addEventListener("load", () => {
        this.uploadImage = reader.result;
        this.fileList = [];
        this.fileList = [
          {
            uid: "-1",
            name: file.name,
            status: "done",
            url: reader.result
          }
        ];
      });
      reader.readAsDataURL(file);
    },
    // 
    handleUploadPreview(file) {
      this.previewImage = file.url || file.thumbUrl;
      this.previewVisible = true;
    },
    // 
    onCascaderChange(codeList, dataList) {
      if (dataList) {
        this.cascaderResult = dataList.map(i => i.label);
      } else this.cascaderResult = null;
    },
    async api(fn, params) {
      try {
        let res = await fn(params);
        this.$parent.updateUserList();
        this.$message.success(res.msg);
        this.visible = false;
      } catch (error) {
        window.console.log(error);
      }
    },
    //
    async apiGetRSA(values) {
      try {
        let res = await getRSA();
        this.$rsa.setPublicKey(res.data.public_key);
        let params = {
          userId: this.isEdit ? this.uid : undefined,
          orgParentId: 0, // ID
          parentUserId: "0", // ID
          nick: values.nick, // 
          mobile: values.mobile, // 
          email: values.email, // 
          realName: values.realName, // 
          position: values.position, // 
          orgName: values.orgName, // 
          configValue: values.configValue, // 
          domain: values.domain, // 
          siteCount: values.siteCount, // 
          permissionIds: values.permissionIds.join(","), // 
          userStatus: values.userStatus, // 
          accountType: values.accountType, // 
          password: undefined, // 
          deadlineEndDate: this.deadlineEndDate
            ? this.deadlineEndDate.format('YYYY-MM-DD') + " 23:59:59"
            : undefined, // 
          imgBase: this.uploadImage || undefined, // logo
          userAddress: this.cascaderResult
            ? `${this.cascaderResult.join("-")}-${this.details || ""}`
            : undefined
        };
        if (values.domain === "lingxe.anbai.com" && this.isVip)
          return this.$message.error(" lingxe.anbai.com");

        if (this.isEdit) {
          if (values.password !== this.record.password) {
            let password = this.$rsa.encrypt(values.password);
            if (!password) return this.$message.error("");
            params.password = password;
          }
          this.api(updateUser, params); // 
        } else {
          let password = this.$rsa.encrypt(values.password);
          if (!password) return this.$message.error("");
          params.password = password;
          this.api(addUser, params); // 
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    // 
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        // console.log(values);
        if (!err) this.apiGetRSA(values);
      });
    },
    showModal() {
      this.visible = true;
      this.form.resetFields();
    },
    handleOk(e) {
      window.console.log(e);
      this.visible = false;
    }
  }
};
</script>

