<template>
  <a-modal
    id="addUserModal"
    :title="isEdit ? '' : ''"
    v-model="visible"
    width="800px"
    :footer="null"
  >
    <a-form class="kfc" :form="form">
      <a-row>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'nick',
                { rules: [{ required: true, message: '' }] }
              ]"
              placeholder="(20)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              minlength="6"
              :type="isEdit ? 'password' : 'text'"
              v-decorator="['password', passwordRules]"
              placeholder="(6)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="200"
              v-decorator="['email', emailRules]"
              placeholder=""
            />
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="11"
              v-decorator="['mobile', phoneRules]"
              placeholder="(11)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="10"
              v-decorator="[
                'realName',
                { rules: [{ required: false, message: '' }] }
              ]"
              placeholder="(10)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'position',
                { rules: [{ required: false, message: '' }] }
              ]"
              placeholder="(20)"
            />
          </a-form-item>
        </a-col>
      </a-row>

      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-tree-select
            key="arm"
            :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
            v-decorator="[
              'orgId',
              { rules: [{ required: true, message: '' }] }
            ]"
            :treeData="treeArch"
            placeholder=""
            treeDefaultExpandAll
          ></a-tree-select>
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-input-search
            disabled
            v-model="oldAddress"
            v-if="hideCascader"
            @search="hideCascader = false"
          >
            <a-button slot="enterButton"></a-button>
          </a-input-search>

          <a-cascader
            v-if="!hideCascader"
            v-decorator="['cascader', cascaderRules]"
            :options="options"
            changeOnSelect
            :loadData="loadCascaderData"
            @change="onCascaderChange"
            placeholder=""
          />
        </a-form-item>

        <a-row class="ant-form-item" v-if="cascaderResult !== null">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-input
              v-model="details"
              placeholder="(200)"
            />
          </a-col>
        </a-row>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-tree-select
            v-decorator="[
              'permissionIds',
              {
                rules: [{ required: true, message: '' }],
                initialValue: [17]
              }
            ]"
            style="width: 100%"
            :treeData="authModule"
            treeCheckable
            @change="onChangeAuth"
            :showCheckedStrategy="SHOW_PARENT"
            searchPlaceholder=""
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label=""
        >
          <a-radio-group
            v-decorator="[
              'userStatus',
              { rules: [{ required: true, message: '' }] }
            ]"
          >
            <a-radio :value="1"></a-radio>
            <a-radio :value="0"></a-radio>
          </a-radio-group>
        </a-form-item>
        <br />
        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-button @click="visible = false"></a-button
            >&nbsp;&nbsp;&nbsp;&nbsp;
            <a-button type="primary" @click="handleSubmit">{{
              isEdit ? "" : ""
            }}</a-button>
          </a-col>
        </a-row>
      </a-row>
    </a-form>
  </a-modal>
</template>

<script>
import {
  phoneRules,
  emailRules,
  passwordRules,
  cascaderRules
} from "../plugins/data.js";
import { addSubUser, updateSubUser, getCite, getRSA } from "@/api/api.js";
import { TreeSelect } from "ant-design-vue";

const SHOW_PARENT = TreeSelect.SHOW_PARENT;
export default {
  data() {
    return {
      phoneRules,
      emailRules,
      passwordRules,
      cascaderRules,
      SHOW_PARENT,
      formItemLayout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 }
      },
      formItemLayout2: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      visible: false,
      form: this.$form.createForm(this),
      record: null,
      uid: null, // ID
      isEdit: false, // 
      hideCascader: false, // 
      options: [], // 
      cascaderResult: null, // 
      authModule: [], // 
      treeArch: [], // 
      details: null // 
    };
  },
  watch: {
    visible: function(value) {
      if (value === false) {
        this.uid = null;
        this.form.resetFields();
        this.isEdit = false;
        this.hideCascader = false; // 
        this.oldAddress = null; // 
      }
    }
  },
  methods: {
    onChangeAuth(value) {
      // console.log("onChange ", value);
      this.$nextTick(() => {
        if (value[0] !== 18) {
          this.form.setFieldsValue({
            permissionIds: value.concat([17])
          });
        }
      });
    },
    initForm(record, auth) {
      setTimeout(() => {
        (this.record = record), (this.uid = record.user_id);
        this.isEdit = true;
        this.hideCascader = true; // 
        this.oldAddress = record.user_address; // 
        this.form.setFieldsValue({
          realName: record.real_name, // 
          userStatus: record.user_status, // 
          position: record.position, // 

          mobile: record.mobile, // 
          email: record.email, // 
          nick: record.nick, // 
          password: record.password, // 
          orgId: `${record.org_name}-${record.org_id}`,
          permissionIds: auth.map(i => i.permission_id) // 
        });
      });
    },
    async api(fn, params) {
      try {
        let res = await fn(params);
        this.$parent.updateUserList();
        this.$message.success(res.msg);
        this.visible = false;
      } catch (error) {
        window.console.log(error);
      }
    },
    //
    async apiGetRSA(values) {
      try {
        let res = await getRSA();
        this.$rsa.setPublicKey(res.data.public_key);

        let params = {
          userId: this.isEdit ? this.uid : undefined,
          parentUserId: this.$route.query.id, // 
          orgId: Number(values.orgId.split("-").pop()), // 
          realName: values.realName, // 
          accountType: 0, // 
          mobile: values.mobile, // 
          email: values.email, // 
          nick: values.nick, // 
          password: undefined, // 
          permissionIds: values.permissionIds.join(","), // 
          userStatus: Number(values.userStatus), // 
          position: values.position, // 
          userAddress: this.cascaderResult
            ? `${this.cascaderResult.join("-")}-${this.details || ""}`
            : undefined // 
        };

        if (this.isEdit) {
          if (values.password !== this.record.password) {
            let password = this.$rsa.encrypt(values.password);
            if (!password) return this.$message.error("");
            params.password = password;
          }
          this.api(updateSubUser, params); // 
        } else {
          let password = this.$rsa.encrypt(values.password);
          if (!password) return this.$message.error("");
          params.password = password;
          this.api(addSubUser, params); // 
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    async loadCascaderData(selectedOptions) {
      try {
        const targetOption = selectedOptions[selectedOptions.length - 1];
        targetOption.loading = true;

        let res = await getCite({
          postCode: targetOption.value
        });

        if (res.valid) {
          targetOption.loading = false;
          targetOption.children = res.data.map(item => {
            return {
              value: item.post_code,
              label: item.city_name,
              deep: targetOption.deep + 1,
              isLeaf: targetOption.deep + 1 > 1
            };
          });

          this.options = [...this.options];
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    // 
    onCascaderChange(codeList, dataList) {
      if (dataList) {
        this.cascaderResult = dataList.map(i => i.label);
      } else {
        this.cascaderResult = null;
      }
    },
    // 
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) this.apiGetRSA(values);
      });
    }
  }
};
</script>

<style lang="less">
.ant-modal {
  .ant-form-item {
  }
}
</style>
