<template>
  <div id="user">
    <!--  -->
    <div class="table-opts">
      <a-form class="table-search" layout="inline">
        <a-form-item>
          <a-button class="bgGray" @click="showModal"></a-button>
        </a-form-item>
        <a-form-item>
          <a-button
            class="bgGray"
            :loading="activeUserBtnLoading"
            :disabled="selectedRowKeys.length === 0"
            @click="apiUpdateUserStatus('enable', selectedRowKeys.join(','))"
            ></a-button
          >
        </a-form-item>
        <a-form-item>
          <a-button
            class="bgGray"
            :loading="unactiveUserBtnLoading"
            :disabled="selectedRowKeys.length === 0"
            @click="apiUpdateUserStatus('pending', selectedRowKeys.join(','))"
            ></a-button
          >
        </a-form-item>

        <a-form-item class="pull-right" style="margin:0">
          <a-button type="primary" @click="onSearch"></a-button>
        </a-form-item>
        <a-form-item class="pull-right">
          <a-input allowClear v-model="orgName" placeholder="" />
        </a-form-item>
        <a-form-item class="pull-right">
          <a-select
            v-model="userStatus"
            placeholder=""
            style="width: 120px"
            allowClear
          >
            <a-select-option value="0"></a-select-option>
            <a-select-option value="1"></a-select-option>
            <a-select-option value="2"></a-select-option>
          </a-select>
        </a-form-item>
      </a-form>
    </div>
    <!--  -->
    <a-table
      :loading="table.loading"
      :pagination="false"
      :columns="columns"
      :dataSource="table.list"
      :rowSelection="rowSelection"
    >
      <div slot="user_status" slot-scope="text, record">
        <span v-if="record.user_status === 0"></span>
        <span v-if="record.user_status === 1"></span>
        <span v-if="record.user_status === 2"></span>
        <span v-if="record.user_status === 3"></span>
      </div>
      <div slot="operation" slot-scope="text, record">
        <!-- user_status (0:,1::2:) -->

        <a-icon
          v-if="record.user_status === 0 || record.user_status === 3"
          class="iconStyle"
          type="safety-certificate"
          title=""
          @click="apiUpdateUserStatus('enable', record.user_id)"
        />
        <a-icon
          v-if="record.user_status === 0"
          class="iconStyle"
          type="stop"
          @click="apiUpdateUserStatus('refuse', record.user_id)"
          title=""
        />

        <a-icon
          v-if="record.user_status === 1"
          class="iconStyle"
          type="edit"
          title=""
          @click="showModal(record)"
        />
        <a-icon
          v-if="record.user_status === 1"
          class="iconStyle"
          type="team"
          title=""
          @click="$router.push({ name: 'arch', query: { id: record.user_id } })"
        />
        <!-- <a-button-group v-if="record.user_status!==0"> -->
        <a-switch
          v-if="record.user_status !== 0 && record.user_status !== 3"
          checkedChildren=""
          unCheckedChildren=""
          :checked="record.user_status === 1"
          title=""
          @click="
            checked =>
              apiUpdateUserStatus(
                checked ? 'enable' : 'pending',
                record.user_id
              )
          "
        />
        <!-- </a-button-group> -->
      </div>
    </a-table>
    <!--  -->
    <div class="user-pagination">
      <a-pagination
        showSizeChanger
        showQuickJumper
        v-model="table.index"
        :total="table.total"
        :pageSize.sync="table.pageSize"
        :showTotal="
          (total, range) =>
            ` ${total}    ${this.table.index}/${Math.ceil(
              total / this.table.pageSize
            )} `
        "
        @showSizeChange="onPageSizeChange"
        @change="onPageIndexChange"
      ></a-pagination>
    </div>

    <!--  -->
    <AddUserModal ref="aum" />
  </div>
</template>

<script>
import AddUserModal from "@/components/addUserModal.vue";

import { userColumns, fmtAuth } from "@/plugins/data.js";
import {
  userList,
  updateUserStatus,
  authModule,
  getCite,
  getUser,
  getPicture,
  getUserAuth
} from "@/api/api.js";

export default {
  components: {
    AddUserModal
  },
  data() {
    return {
      selectedRowKeys: [],
      columns: userColumns,
      orgName: null, // -
      userStatus: undefined, // - enable:pending: disable: , refuse: 
      activeUserBtnLoading: false, // 
      unactiveUserBtnLoading: false, // 
      table: {
        loading: false,
        list: [],
        total: 0, // 
        index: 1, // 
        pageSize: 10
      }
    };
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        selectedRowKeys,
        onChange: this.onSelectChange,
        onSelection: this.onSelection,
        getCheckboxProps: record => ({
          props: {
            disabled: record.user_status === 0
          }
        })
      };
    }
  },
  created() {
    this.updateUserList();
  },
  methods: {
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRows.map(i => i.user_id);
      // window.console.log(this.selectedRowKeys);
    },
    onSearch() {
      this.table.index = 1;
      this.updateUserList();
    },
    // 
    onPageIndexChange(page, pageSize) {
      this.table.index = page;
      this.table.pageSize = pageSize;
      this.updateUserList();
      // window.console.log(page, pageSize);
    },
    // 
    onPageSizeChange(current, pageSize) {
      this.table.index = 1;
      this.table.pageSize = pageSize;
      this.updateUserList();
      // window.console.log(current, pageSize);
    },
    // 
    async apiUpdateUserStatus(status, userIds) {
      this.table.loading = true;
      try {
        let res = await updateUserStatus({
          status,
          userIds
        });
        if (!res.valid) return this.showMsg(res.msg);
        this.updateUserList();
        this.$message.success(res.msg);
      } catch (error) {
        window.console.log(error);
      }

      this.table.loading = false;
      this.activeUserBtnLoading = false;
      this.unactiveUserBtnLoading = false;
    },
    // 
    async updateUserList() {
      this.table.loading = true;
      try {
        let res = await userList({
          pageNum: this.table.index,
          pageSize: this.table.pageSize,
          keyword: this.orgName || undefined,
          userStatus: this.userStatus ? Number(this.userStatus) : undefined
        });

        if (!res.valid) return this.showMsg(res.msg);

        this.table.total = res.data.recordCount;
        this.table.list = res.data.result.map(row => {
          row["key"] = row.user_id;
          row["rowKey"] = row.user_id;
          return row;
        });
        setTimeout(() => (this.selectedRowKeys = []));
      } catch (error) {
        window.console.log(error);
      }
      this.table.loading = false;
    },
    //  =>  || 
    async showModal(record) {
      this.table.loading = true;
      try {
        let auth = await authModule();
        if (!auth.valid) return this.showMsg(auth.msg);
        let cite = await getCite();
        if (!cite.valid) return this.showMsg(cite.msg);

        this.$refs.aum.authModule = fmtAuth(auth.data);
        this.$refs.aum.options = cite.data.map(item => {
          return {
            value: item.post_code,
            label: item.city_name,
            deep: 0,
            isLeaf: false
          };
        });

        if (typeof record.user_id !== "undefined") {
          // 
          let params = { userId: record.user_id };
          let user = await getUser(params);
          if (!user.valid) return this.showMsg(user.msg);

          let auth = await getUserAuth(params);
          if (!auth.valid) return this.showMsg(auth.msg);

          this.$refs.aum.visible = true;
          this.$refs.aum.initForm(user.data, auth.data);
        } else this.$refs.aum.visible = true; // 
      } catch (error) {
        window.console.log(error);
      }
      this.table.loading = false;
    },
    showMsg(msg) {
      this.table.loading = false;
      this.$message.error(msg);
    }
  }
};
</script>
<style lang="less" scoped>
#user {
  .user-pagination {
    text-align: right;
    padding: 30px 0;
  }
  .table-opts {
    padding-bottom: 15px;
    line-height: 1.5;
  }
  .table-search {
    display: inline;
  }
}
</style>