<template>
  <a-modal
    id="addUserModal"
    :title=" defaultId === null ? '':'' "
    v-model="visible"
    @ok="visible = false"
    :footer="null"
  >
    <a-form class="kfc" :form="form" @submit="handleSubmit">
      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          :label="defaultId === null ? '' : ''"
        >
          <a-input
            maxlength="20"
            v-decorator="[ 'name', {initialValue: defaultTitle, rules: [{max:20, required: true, message: '' }]} ]"
            placeholder="(20)"
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          :label="defaultId === null ? '':'' "
        >
          <a-tree-select
            showSearch
            :disabled="defaultValue!==null"
            key="arm"
            v-decorator="[ 'id', {initialValue: defaultValue, rules: [{ required: true, message: '' }]} ]"
            :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
            :treeData="treeData"
            placeholder=""
            treeDefaultExpandAll
          ></a-tree-select>
        </a-form-item>

        <br>
        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-button @click="visible = false"></a-button>&nbsp;&nbsp;&nbsp;&nbsp;
            <a-button type="primary" v-if="defaultId===null" @click.prevent="handleSubmit('add')"></a-button>
            <a-button type="primary" v-else @click.prevent="handleSubmit('edit')"></a-button>
          </a-col>
        </a-row>
      </a-row>
    </a-form>
  </a-modal>
</template>

<script>
import { addArch, updateArch } from "@/api/api.js";
export default {
  data() {
    return {
      formItemLayout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 }
      },
      formItemLayout2: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      visible: false,
      form: this.$form.createForm(this),
      treeData: [], // 
      defaultValue: null, // 
      defaultTitle: null, // 
      defaultId: null, // ID
      pid: 0 // ID
    };
  },
  methods: {
    async api(fn, params) {
      try {
        let res = await fn(params);
        this.$parent.apiGetArch();
        this.$message.success(res.msg);
        this.visible = false;
      } catch (error) {
        window.console.log(error);
      }
    },
    handleSubmit(type) {
      this.form.validateFields((err, values) => {
        if (!err) {
          // console.log("Received values of form: ", values);
          if (type == "edit") {
            this.api(updateArch, {
              companyId: this.$parent.uid,
              orgParentId: this.pid,
              orgName: values.name,
              orgId: Number(values.id.split("-").pop())
            });
          } else if (type == "add") {
            this.api(addArch, {
              companyId: this.$parent.uid,
              orgName: values.name,
              orgParentId: Number(values.id.split("-").pop())
            });
          }
        }
      });
    }
  }
};
</script>

