  .arch-pagination {
    text-align: right;
    padding: 30px 0 30px 0;
  }
  .arch-row {
    &:first-child {
      border-bottom: 1px solid #d9d9d9;
      padding-bottom: 15px;
    }
  }
  .arch-title {
    font-size: 18px;
    color: #666666;
  }
  .arch-right {
    float: left;
    width: calc(100% - 240px);
  }
  .arch-Table {
    padding: 20px 0 0 20px;
    border-left: 1px solid #d9d9d9;
  }
  .arch-left {
    float: left;
    width: 240px;
    border-right: 1px solid #d9d9d9;
    margin-right: -1px;
  }
  .arch-tree {
    margin-right: 16px;
    position: relative;
  }
  .addArch {
    font-size: 16px;
    color: @blue;
    text-align: center;
    cursor: pointer;
