<template>
  <div id="home">
    <div class="header">
      <p></p>
      <HeaderUser class="opts"/>
    </div>
    <div class="main">
      <div class="module kfc">
        <div
          class="box"
          v-for="(item,index) in module"
          :key="index"
          :style="boxStyle"
          @click="$router.push({ name: item.router })"
        >
          <div class="content">
            <div class="console-icon">
              <img :src="item.icon" alt="item.name">
            </div>
            <div class="name">{{item.name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderUser from "@/components/headerUser.vue";
import { throttle } from "@/plugins/tool.js";
export default {
  components: {
    HeaderUser
  },
  data() {
    return {
      boxStyle: {
        height: "20%"
      },
      module: [
        {
          name: "",
          icon: require("../images/console-user.png"),
          router: "user"
        },

        {
          name: "",
          icon: require("../images/console-patch.png"),
          router: "patch"
        },
        {
          name: "",
          icon: require("../images/console-app.png"),
          router: "app"
        },
        {
          name: "",
          icon: require("../images/console-log.png"),
          router: "log"
        }
      ]
    };
  },
  mounted() {
    this.$nextTick(() => {
      let resizeBox = () => {
        let box = document.getElementsByClassName("box");
        this.boxStyle.height = box[0].offsetWidth / (300 / 400) + "px";
      };
      resizeBox();
      window.onresize = throttle(resizeBox, 1000, true);
    });
  },
  destroyed() {
    window.onresize = null;
  }
};
</script>
<style lang="less" scoped>
@import "../common/mixin.less";
#home {
  .opts {
    color: white;
  }
  .header {
    height: 80px;
    background: @blue;
    position: relative;
    line-height: 80px;
    > p {
      height: 80px;
      line-height: 80px;
      text-align: center;
      letter-spacing: 2px;
      color: white;
      font-size: 24px;
    }
  }
  .main {
    .module {
      text-align: center;
      padding: 100px 30px;
      .box {
        display: inline-block;
        width: 20%;
        max-width: 320px;
        line-height: 50px;
        line-height: 50px;
        letter-spacing: 1px;
        padding: 100px 0;
        background: white;
        box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0);
        border-radius: 4px;
        cursor: pointer;
        margin: 15px;
        transition: all 60ms;
        font-size: 20px;
        position: relative;
        .content {
          position: absolute;
          width: 100%;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
        }
        img {
          width: 33%;
          height: auto;
        }
        &:hover {
          box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.1);
          color: @blue;
        }
      }
    }
  }
}
</style>


