function getAllMemberUsers(options) {
    let body = JSON.parse(options.body);
    let res = getStore('getAllMemberUsers');

    if (body.keyword) {
        res.data.result = res.data.result.filter(item => {
            if (!item.org_name) return false;
            return item.org_name.indexOf(body.keyword) !== -1;
        });
    }

    if (typeof body.userStatus !== 'undefined') {
        res.data.result = res.data.result.filter(
            item => item.user_status === body.userStatus,
        );
    }

    res.data.recordCount = res.data.result.length;

    res.data.result = res.data.result.slice(
        body.pageSize * (body.pageNum - 1),
        body.pageSize * body.pageNum,
    );

    return res;
}
