function updateUserStatus(options) {
    let data = JSON.parse(options.body);
    let res = getStore('getAllMemberUsers');
    let allUser = res.data.result;
    console.log(data);
    //------------------------
    let all_status = {
        enable: 1,
        pending: 2,
        disable: 3,
        refuse: 0,
    };

    allUser.forEach(user => {
        if (data.userIds.split(',').indexOf(user.user_id) !== -1) {
            user.user_status = all_status[data.status];
        }
    });

    setStore('getAllMemberUsers', res);

    return {
        code: -1,
        data: allUser,
        msg: '',
        valid: true,
    };
}

