<template>
  <div id="login">
    <div class="logo">
      <img src="../images/logo-lg.png" alt>
    </div>
    <div class="main">
      <p class="head title"></p>
      <div class="content">
        <div class="form-name">
          <span></span>
        </div>
        <a-form
          id="components-form-demo-normal-login"
          :form="form"
          class="login-form"
          @submit="handleSubmit"
        >
          <a-form-item>
            <a-input
              class="login-input"
              v-decorator="[ 'login', { rules: [{ required: true, message: '!' }] } ]"
              placeholder=""
            >
              <a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)"/>
            </a-input>
          </a-form-item>
          <a-form-item>
            <a-input
              class="login-input"
              v-decorator="[ 'password', passwordRules ]"
              type="password"
              placeholder=""
            >
              <a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)"/>
            </a-input>
          </a-form-item>
          <a-form-item class="addBottom">
            <a-input
              class="login-input"
              placeholder=""
              maxlength="6"
              v-decorator="[ 'code', { rules: [{ required: true, message: '!' }] } ]"
            >
              <a-icon slot="prefix" type="safety-certificate" style="color: rgba(0,0,0,.25)"/>
              <span class="code" @click="apiCaptcha" slot="addonAfter">
                <img v-if="captcha" width="100%" :src="captcha" alt="">

                <a-icon v-else type="loading"/>
              </span>
            </a-input>
            <span class="changeCode" @click="apiCaptcha"></span>
          </a-form-item>
          <a-form-item>
            <a-button
              type="primary"
              html-type="submit"
              class="login-form-button"
              @click="handleSubmit"
              :disabled="loginDisabled"
            > </a-button>
          </a-form-item>
        </a-form>
      </div>
    </div>
  </div>
</template>

<script>
import { login, captcha, getRSA } from "../api/api.js";

import { passwordRules } from "../plugins/data.js";
export default {
  data() {
    return {
      form: this.$form.createForm(this),
      loginDisabled: false,
      captchaToken: null,
      captcha: null,
      passwordRules
    };
  },
  created() {
    this.apiCaptcha();
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) this.apiGetRSA(values);
      });
    },
    async apiGetRSA(values) {
      try {
        let res = await getRSA();
        if (!res.valid) return this.$message.error(res.msg);

        let RSA_PUBLIC_KEY = res.data.public_key;
        this.$rsa.setPublicKey(RSA_PUBLIC_KEY);

        let login = this.$rsa.encrypt(values.login);
        let password = this.$rsa.encrypt(values.password);

        if (!login || !password)
          return this.$message.error("");

        this.apiLogin({
          captcha: values.code,
          captchaToken: this.captchaToken,
          login,
          password
        });
      } catch (error) {
        window.console.log(error);
      }
    },
    async apiCaptcha() {
      try {
        let res = await captcha();
        if (!res.valid) return this.$message.error(res.msg);

        this.captcha = "data:image/png;base64," + res.data.captcha;
        this.captchaToken = res.data["captcha-token"];
      } catch (error) {
        window.console.log(error);
      }
    },
    async apiLogin(params) {
      this.loginDisabled = true;
      try {
        let res = await login(params);
        if (!res.valid) return this.$message.error(res.msg);

        localStorage.setItem("token", res.data.token);
        localStorage.setItem("info", JSON.stringify(res.data.info));
        localStorage.setItem("tokentimemap", Date.parse(new Date()));

        this.$router.replace({ name: "console" });
      } catch (error) {
        window.console.log(error);
      }
      this.loginDisabled = false;
      this.apiCaptcha();
    }
  }
};
</script>
