<template>
  <div id="admin" class="kfc">
    <div :style="siderStyle">
      <div class="logo">{{collapsed ? '' : ''}}</div>
      <a-menu
        class="admin-menu"
        :defaultSelectedKeys="['1']"
        :defaultOpenKeys="['userMenu']"
        mode="inline"
        theme="dark"
        :inlineCollapsed="collapsed"
      >
        <a-sub-menu key="userMenu">
          <span slot="title">
            <a-icon type="user"/>
            <span></span>
          </span>
          <a-menu-item key="1" @click="$router.push({name:'user'})"></a-menu-item>
        </a-sub-menu>
      </a-menu>
    </div>
    <div :style="mianStyle">
      <a-layout style="height:100%">
        <a-layout-header style="background: #fff; padding: 0;position:relative">
          <a-icon
            class="trigger"
            :type="collapsed ? 'menu-unfold' : 'menu-fold'"
            @click="()=> collapsed = !collapsed"
          />
          <HeaderUser class="opts"/>
        </a-layout-header>
        <a-layout-content class="layoutMain">
          <a-locale-provider :locale="locale">
            <router-view class="routerView"></router-view>
          </a-locale-provider>
        </a-layout-content>
      </a-layout>
    </div>
  </div>
</template>
<script>
import zhCN from "ant-design-vue/lib/locale-provider/zh_CN";
import HeaderUser from "@/components/headerUser.vue";
export default {
  components: {
    HeaderUser
  },
  data() {
    return {
      locale: zhCN,
      collapsed: false,
      isBreadrouter: false
    };
  },
  computed: {
    siderStyle() {
      return {
        display: "inline-block",
        width: this.collapsed == false ? "200px" : "80px",
        float: "left",
        height: "100%"
      };
    },
    mianStyle() {
      return {
        display: "inline-block",
        width:
          this.collapsed == false ? "calc(100% - 200px)" : "calc(100% - 80px)",
        float: "right",
        height: "100%"
      };
    }
  }
};
</script>

