export const fmtArch = (orignData, deepLimit) => {
    let tree = [];
    let loop = (arr, pid, deep) => {
        deep = deep || 1;
        orignData.forEach(item => {
            if (item.org_parent_id === pid && deep <= deepLimit) {
                arr.push({
                    deep, // 
                    isEdit: deep !== 1,
                    isAddChild: deep < deepLimit, // 12345
                    isRemove:
                        deep !== 1 &&
                        typeof orignData.find(
                            i => i.org_parent_id === item.org_id,
                        ) === 'undefined', // 
                    title: item.org_name, // 
                    key: item.org_id, // 
                    pid: pid,
                    value: item.org_name + '-' + item.org_id, // 
                    scopedSlots: {
                        title: 'title',
                    }, // 
                    children: [],
                });
                loop(
                    arr[arr.length - 1].children,
                    item.org_id,
                    ++arr[arr.length - 1].deep,
                );
            }
        });
    };
    loop(tree, 0);
    return tree;
};
